<template>
  <div>
    <da-panel overflow="auto" :noHead="true" padding="lbr" min-height="37px" :border="false">
      <da-radio-select class="pad-10" v-model="rdsEnergySubitem" :maxNum="5" :option-lists="energySubitemList" label="name" value-key="id"></da-radio-select>
      <daCusTime v-model="timeSpan" :time-radio="timeArray" :time-type.sync="timeType" @change="changeTime"></daCusTime>
      <button type="button" class="el-button el-button--primary" @click="search">
        <span>查询</span>
      </button>
    </da-panel>
    <div class="ce_cont_wrap">
      <carbon-count class="ce_cont_layo" v-for="(item, index) in egySaveData" width="25%" :key="index" :bg-color="bgColor[index]" :data-obj="item"></carbon-count>
    </div>

    <da-equal-panel height="auto">
      <div slot="left">
        <da-radio-block v-model="rdbSubitem" :data-lists="subitemList" label="subitemName" value-key="id" @change="subitemChange"></da-radio-block>
        <da-panel overflow="auto" padding="t" :noHead="true" height="407px">
          <da-tree v-model="treeData" :data-lists="subitemTreeData" :show-checkbox="true" :check-strictly="true" :defaultKeys="treeDefaultKeys" :default-expand="true"></da-tree>
        </da-panel>
      </div>

      <div slot="right">

        <da-panel overflow="auto" padding="lr" :noHead="true" height="auto">
          <da-line-chart id="egySavingAmount" type="bar" label="实际节能量" x-axis-key="name" value-key="value" :date-time="egySavingAmountTime" :data-lists="egySavingAmountData" :lineData="targetBenchmark" height="453px"></da-line-chart>
        </da-panel>
      </div>
      <!-- <da-analysis-panel>
        目标节能总量：{{targetAmounts}} 吨标煤<br> 实际节能总量：{{actualAmounts}} 吨标煤<br> 目标达成率：{{targetPercent}}%
      </da-analysis-panel> -->
    </da-equal-panel>

  </div>
</template>
<script>
import carbonCount from "../analysis/components/carbonCount";
import daCusTime from "../template/customTimeSelect";
import { reduceById, calcTime, cycleTime } from "../../../util/util";
export default {
  components: {
    carbonCount,
    daCusTime
  },
  data() {
    let endTime = this.$moment(new Date(), "YYYY-MM-DD");
    let cycle = this.$store.state.targetConstant.targetCycle;
    let mStime = calcTime(endTime, -12, cycle["month"], "YYYY-MM-DD");
    let yStime = calcTime(endTime, -5, cycle["year"], "YYYY-MM-DD");
    return {
      timeArray: [
        {
          name: "最近12月",
          time: [mStime, endTime],
          type: cycle["month"]
        },
        {
          name: "最近5年",
          time: [yStime, endTime],
          type: cycle["year"]
        }
      ],
      rdsEnergySubitem: "",
      energySubitemList: [],
      timeType: 5,
      egySaveData: [
        { name: "目标节能量", value: "-", unit: "吨标煤" },
        { name: "实际节能量", value: "-", unit: "吨标煤" },
        { name: "达成率", value: "-", unit: "%" },
        { name: "约节省成本", value: "-", unit: "万元" }
      ], //节省的能源数据
      bgColor: [], //色块样式配置
      rdbSubitem: 1, //选择的分类
      subitemList: [
        { id: 1, subitemName: "组织架构" },
        { id: 2, subitemName: "设备" }
      ], //分类的列表
      treeData: "", //树数据的ID
      treeDefaultKeys: {},
      subitemTreeData: [], //列表树数据
      timeSpan: [], // 时间区间
      beginTime: "", //查询的开始时间
      finishTime: "", //查询的结束时间
      egySavingAmountTime: "", //节能的统计的时间
      egySavingAmountData: [], //节能的统计数据
      targetBenchmark: {},
      energyIds: "", //分类能源ID数组
      savaEnergyCost: [], //各种能源节能的量
      arrayEnergyIds: [] //分类能源ID
    };
  },
  methods: {
    //初始化页面基础数据
    inintData() {
      this.treeData = this.rdsEnergySubitem = this.$store.state.targetConstant.energy.compreEnergyConsum;
      this.timeType = this.$store.state.targetConstant.targetCycle.month;
      let cycle = this.$store.state.targetConstant.targetCycle;
      //设置默认时间
      this.beginTime = calcTime(
        this.$moment(new Date(), "YYYY-MM-DD"),
        -12,
        cycle["month"],
        "YYYY-MM-DD"
      );
      this.finishTime = this.$moment(new Date(), "YYYY-MM-DD");
      this.bgColor = ["#548630", "#54ddde", "#456dca", "#a578d3"];
      this.getSubitemTreeData();
      this.getEnergyType();
    },
    //加载页面数据
    loadData() {
      let orgId = this.$store.state.targetConstant.orgId.enterprise;
      this.getData(orgId);
    },
    //点击分类按钮
    subitemChange() {
      if (this.rdbSubitem == 1) {
        this.urlParams = "depart";
      }
      if (this.rdbSubitem == 2) {
        this.urlParams = "equip";
      }
      this.getSubitemTreeData(this.urlParams);
      this.loadData();
    },
    //获取树列表内容
    getSubitemTreeData() {
      let params = "";
      if (this.rdbSubitem == 1) {
        params = "departTree";
      }
      if (this.rdbSubitem == 2) {
        params = "equip";
      }
      let paramsObj = {
        title: "树形条件",
        isAll: false,
        groupBys: "0100000000"
      };
      this.$ajax({
        url: "rest/analysisData/" + params,
        method: "get",
        params: paramsObj
      }).then(res => {
        if (res.data.status) {
          this.treeDefaultKeys = {
            nodeKey: "id",
            checkedKeys: [res.data.records[0].id],
            expandedKeys: []
          };
          this.subitemTreeData = res.data.records;
          this.treeData = res.data.records[0].id.toString();
        }
      });
    },
    //获得能源种类
    getEnergyType() {
      this.$ajax({
        url: "rest/analysisData/energy",
        method: "get",
        params: {
          title: "获得能源种类" // String,功能标题
        }
      }).then(res => {
        if (res.data.status) {
          if (res.data.records !== null) {
            let compreEnergyConsumId = this.$store.state.targetConstant.energy
              .compreEnergyConsum;
            this.$each(res.data.records, item => {
              this.energySubitemList.push({
                name: item.name,
                id: item.id
              });
            });
            let arrayIds = [];
            for (let i = 0; i < res.data.records.length; i++) {
              if (
                i < res.data.records.length - 1 &&
                res.data.records[i].id !== compreEnergyConsumId
              ) {
                arrayIds.push(res.data.records[i].id);
              }
            }
            this.energyIds = arrayIds.toString();
            this.arrayEnergyIds = arrayIds;
          }
          this.loadData();
        }
      });
    },

    //切换时间
    changeTime() {
      this.beginTime = this.timeSpan[0];
      this.finishTime = this.timeSpan[1];
    },

    //查询节能目标
    getSaveEnergyTarget(orgId) {
      let targetType = this.$store.state.targetConstant.targetType.saveEnergy;
      let contentTypes = this.$store.state.targetConstant.targetContentType
        .energy;
      return this.$ajax({
        url: "rest/analysisData/targetBenchmarkData",
        method: "get",
        params: {
          title: "计划节能量", // String,功能标题
          orgIds: orgId, // String,部门ID
          targetCycle: this.timeType, // String,指标周期
          targetTypes: targetType, // String,指标类型
          contentTypes: contentTypes, // String,统计内容类型
          contentIds: this.rdsEnergySubitem, // String,统计内容ID
          objTypes: null, // String,指标关联对象类型
          objTypeValues: null, // String,指标关联对象值
          isQueryAll: false, // Boolean,是否所有指标数据
          benchmarkQuota: "001", //长度为3的字符串"111"表示是否查询[标杆][定额][目标]
          startTime: this.beginTime, // String,开始时间
          endTime: this.finishTime, // String,结束时间
          isOrgId: false, // Boolean,是否返回部门ID
          isContentType: false, // Boolean,是否返回指标统计内容类型ID
          isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
        }
      });
    },
    //查询实际节能量
    getCompreEnergy(orgId) {
      let targetType = this.$store.state.targetConstant.targetType.saveEnergy;
      let contentTypes = this.$store.state.targetConstant.targetContentType
        .energy;
      return this.$ajax({
        url: "rest/analysisData/targetData",
        method: "get",
        params: {
          title: "查询实际节能量", // String,功能标题
          orgIds: orgId, // String,部门ID
          targetCycle: this.timeType, // String,指标周期
          targetTypes: targetType, // String,指标类型
          contentTypes: contentTypes, // String,统计内容类型
          contentIds: this.rdsEnergySubitem, // String,统计内容ID
          objTypes: null, // String,指标关联对象类型
          objTypeValues: null, // String,指标关联对象值
          isQueryAll: false, // Boolean,是否所有指标数据
          startTime: this.beginTime, // String,开始时间
          endTime: this.finishTime, // String,结束时间
          isOrgId: false, // Boolean,是否返回部门ID
          isContentType: true, // Boolean,是否返回指标统计内容类型ID
          isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
        }
      });
    },
    //查询各种能源节能量
    getSaveEnergy(orgId) {
      let targetType = this.$store.state.targetConstant.targetType.saveEnergy;
      let contentTypes = this.$store.state.targetConstant.targetContentType
        .energy;
      let compreEnergyIds = this.$store.state.targetConstant.energy
        .compreEnergyConsum;
      let tempenergyIds;
      if (this.rdsEnergySubitem == compreEnergyIds) {
        tempenergyIds = this.energyIds;
      } else {
        tempenergyIds = this.rdsEnergySubitem;
      }
      return this.$ajax({
        url: "rest/analysisData/targetData",
        method: "get",
        params: {
          title: "每种能源实际节能量", // String,功能标题
          orgIds: orgId, // String,部门ID
          targetCycle: this.timeType, // String,指标周期
          targetTypes: targetType, // String,指标类型
          contentTypes: contentTypes, // String,统计内容类型
          contentIds: tempenergyIds, // String,统计内容ID
          objTypes: null, // String,指标关联对象类型
          objTypeValues: null, // String,指标关联对象值
          isQueryAll: false, // Boolean,是否所有指标数据
          startTime: this.beginTime, // String,开始时间
          endTime: this.finishTime, // String,结束时间
          isOrgId: false, // Boolean,是否返回部门ID
          isContentType: true, // Boolean,是否返回指标统计内容类型ID
          isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
        }
      });
    },
    //计算节约成本
    calculateSaveCost() {
      let orgId = this.$store.state.targetConstant.orgId.enterprise;
      let targetType = this.$store.state.targetConstant.targetType.energyPrice;
      let contentTypes = this.$store.state.targetConstant.targetContentType
        .energy;
      let targetCycle = this.$store.state.targetConstant.targetCycle.day;

      return this.$ajax({
        url: "rest/analysisData/targetData",
        method: "get",
        params: {
          title: "求能源的单价", // String,功能标题
          orgIds: orgId, // String,部门ID
          targetCycle: targetCycle, // String,指标周期
          targetTypes: targetType, // String,指标类型
          contentTypes: contentTypes, // String,统计内容类型
          contentIds: this.energyIds, // String,统计内容ID
          objTypes: null, // String,指标关联对象类型
          objTypeValues: null, // String,指标关联对象值
          isQueryAll: true, // Boolean,是否所有指标数据
          startTime: this.beginTime,
          endTime: this.finishTime, // String,结束时间
          isOrgId: false, // Boolean,是否返回部门ID
          isContentType: true, // Boolean,是否返回指标统计内容类型ID
          isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
        }
      });
    },
    //统一查询
    getData(orgId) {
      this.$ajax
        .all([
          this.getSaveEnergyTarget(orgId),
          this.getCompreEnergy(orgId),
          this.getSaveEnergy(orgId),
          this.calculateSaveCost()
        ])
        .then(res => {
          if (res[0].data.status && res[0].data.records.target.length > 0) {
            //填充柱形图数据
            this.targetBenchmark = res[0].data.records;
            //统计目标节能量
            let sum = 0;
            this.egySaveData[0].unit = res[0].data.records.target[0].targetUnit;
            for (let i = 0; i < res[0].data.records.target.length; i++) {
              sum += res[0].data.records.target[i].dataValue;
            }
            this.egySaveData[0].value = sum.toFixed(2);
          } else {
            this.targetBenchmark = {};
            this.egySaveData[0].value = "-";
          }
          if (res[1].data.status && res[1].data.records.length > 0) {
            //填充柱形图数据
            this.egySavingAmountData = reduceById(
              res[1].data.records,
              "time",
              "dataValue",
              "targetUnit"
            );
            //计算实际节能量
            let sum = 0;
            this.egySaveData[1].unit = res[1].data.records[0].targetUnit;
            for (let i = 0; i < res[1].data.records.length; i++) {
              sum += res[1].data.records[i].dataValue;
            }
            this.egySaveData[1].value = sum.toFixed(2);
            //达成率
            if (
              this.egySaveData[0].value < 0 ||
              this.egySaveData[0].value > 0
            ) {
              let num =
                parseFloat(this.egySaveData[1].value) /
                parseFloat(this.egySaveData[0].value);
              this.egySaveData[2].value = (num * 100).toFixed(2);
            } else {
              this.egySaveData[2].value = "-";
            }
          } else {
            this.egySavingAmountData = [];
            this.egySaveData[1].value = "-";
            this.egySaveData[2].value = "-";
            this.egySaveData[3].value = "-";
          }
          if (res[2].data.status) {
            this.savaEnergyCost = res[2].data.records;
          }
          if (res[3].data.status) {
            //筛选能源单价
            let energyPrice = [];
            let tempData = [];
            //求每种能源的单价
            for (let i = 0; i < this.arrayEnergyIds.length; i++) {
              tempData = res[3].data.records.filter(item => {
                return this.arrayEnergyIds[i] == item.contentId;
              });
              if (tempData.length > 0) {
                energyPrice.push({
                  id: this.arrayEnergyIds[i],
                  value: tempData[tempData.length - 1].dataValue
                });
              }
            }
            let tempEnergyData = [];
            //算每种能源的总价
            for (let i = 0; i < energyPrice.length; i++) {
              let arrayData = [];
              arrayData = this.savaEnergyCost.filter(item => {
                return (item.contentId = energyPrice[i].id);
              });
              let sum = 0;
              for (let j = 0; j < arrayData.length; j++) {
                sum += arrayData[j].dataValue * energyPrice[i].value;
              }
              tempEnergyData.push({
                id: energyPrice[i].id,
                value: sum
              });
            }

            //对各种能源总价求和
            let result = 0;
            for (let k = 0; k < tempEnergyData.length; k++) {
              result += tempEnergyData[k].value;
            }
            if (result > 0 || result < 0) {
              this.egySaveData[3].value = (result / 10000).toFixed(2);
            } else {
              this.egySaveData[3].value = "-";
            }
          }
        });
    },
    search() {
      this.egySaveData.forEach(element => {
        element.value = "-";
      });
      if (this.treeData == "") {
        this.treeData = null;
      }
      this.getData(this.treeData);
    }
  },

  mounted() {
    this.inintData();
    // this.loadData();
  }
};
</script>

<style scoped>
.ce_cont_wrap {
  margin: 0 -10px;
}
</style>



