<template>
  <div class="cus-time-wrap inblock">
    <el-radio-group class="mar-lr-5" v-model="subitemTime" @change="subitemTimeChange">
      <!-- <el-radio class="cus-time-raido" :label="3">最近十五天</el-radio>
      <el-radio class="cus-time-raido" :label="6">最近六个月</el-radio> -->
      <el-radio v-for="(item,index) in timeRadio" :key="index" class="cus-time-raido" :label="index">{{item[attr.label]}}</el-radio>
      <el-radio class="cus-time-raido" :label="-1">自定义</el-radio>
    </el-radio-group>
    <da-time-range class="inblock mar-lr-5" v-model="timeSpan" v-show="subitemTime==-1" :time-type.sync="type" :show-time="[3,5,8]" :label="{ sLable: '统计期:', eLabel: '到' }" :is-picker-end="isPickerEnd" @change="timeSpanSelect" width="350px"></da-time-range>
  </div>
</template>


<script>
import { calcTime, cycleTime } from "../../../util/util";
export default {
  props: {
    value: {
      type: Array
    },
    timeRadio: {
      type: Array,
      default() {
        let endTime = this.$moment(new Date(), "YYYY-MM-DD");
        let cycle = this.$store.state.targetConstant.targetCycle;
        let dStime = calcTime(endTime, -15, cycle["day"], "YYYY-MM-DD");
        let mStime = calcTime(endTime, -6, cycle["month"], "YYYY-MM-DD");
        return [
          {
            name: "最近十五天",
            time: [dStime, endTime],
            type: cycle["day"]
          },
          {
            name: "最近六个月",
            time: [mStime, endTime],
            type: cycle["month"]
          }
        ];
      }
    },
    attr: {
      type: Object,
      default() {
        return {
          label: "name",
          time: "time",
          type: "type"
        };
      }
    },
    timeType: {
      type: [Number,String], // 3为日，5为月，8为年
      default: 3
    },
    // 结束时间是否关联
    isPickerEnd:{
      type: Boolean,
      default: true
    }
  },
  data() {
    let time = [];
    let subitemTime = -1; // raido值，即下标
    let type = 3;
    if (this.value.length > 0) {
      time = this.value;
      subitemTime = -1;
      if(this.timeType) {
        type = this.timeType;
      }
    } else {
      if (this.timeType) {
        this.$each(this.timeRadio, (item, index) => {
          if (item.type === this.timeType) {
            subitemTime = index;
          }
        });
      }
      let obj = this.timeCalculation(subitemTime);
      this.$emit("update:timeType", obj.type);
      this.$emit("input", [obj.startTime, obj.endTime]);
      time = [obj.startTime, obj.endTime];
    }
    return {
      cycle: this.$store.state.targetConstant.targetCycle,
      timeSpan: time,
      subitemTime: subitemTime,
      type: type
    };
  },
  methods: {
    subitemTimeChange(labelValue) {
      let obj = this.timeCalculation(labelValue);
      this.$emit("update:timeType", obj.type);
      this.$emit("input", [obj.startTime, obj.endTime]);
      if (this.subitemTime == -1) {
        return;
      }
      this.$emit("change", [obj.startTime, obj.endTime], obj.type);
    },
    // 判断切换的时间类型
    timeCalculation(labelValue) {
      this.subitemTime = labelValue;
      let type = "";
      let startTime = "";
      let endTime = new Date();

      if (labelValue === -1) {
        type = this.type;
        startTime = this.timeSpan[0];
        endTime = this.timeSpan[1];
      } else {
        let data = this.timeRadio[labelValue];
        type = data[this.attr.type];
        startTime = data[this.attr.time][0];
        endTime = data[this.attr.time][1];
      }
      return {
        type,
        startTime,
        endTime
      };
    },
    timeSpanSelect(date, type) {
      this.$emit("update:timeType", type);
      //   this.$emit("input", this.timeSpan);
      if (date && date[0] && date[1]) {
        this.$emit("change", date, type);
      }
    },
    setTime(value) {
      this.timeSpan = value;
      this.subitemTime = 9;
    }
  },
  watch: {
    // timeType() {
    //   console.log(this.timeType);
    //   if (this.timeType !== this.type) {
    //     this.subitemTime = -1;
    //     this.type = this.timeType;
    //     let obj = this.timeCalculation(-1);
    //   }
    // },
    timeSpan: {
      handler() {
        this.$emit("input", this.timeSpan);
      },
      deep: true
    },
    value: {
      handler() {
        // if (
        //   this.value.join(",") !== this.timeSpan.join(",") &&
        //   this.timeType === 9
        // ) {
        //   this.timeSpan = this.value;
        //   this.subitemTimeChange(9);
        // }
      },
      deep: true
    }
  },
  mounted() {
    // let obj = this.timeCalculation(this.timeType);
    // this.$emit("update:timeType", obj.type);
    // this.$emit("input", [obj.startTime, obj.endTime]);
  }
};
</script>

<style scoped>
.cus-time-raido {
  vertical-align: top;
  height: 30px;
  line-height: 30px;
}
.cus-time-wrap > .mar-lr-5 {
  vertical-align: top;
}
</style>




